export default function StructuredData({ products }) {
  // Format price function to handle large numbers
  const formatPriceForSchema = (price) => {
    if (!price) return "0";
    // Remove non-digit characters and return as string
    return String(price).replace(/[^\d]/g, "");
  };

  // Create individual product schema
  const productSchema = products.map((product) => ({
    "@type": "Product",
    "@id": `https://allmaxsport.com/products/${product.slug}#product`,
    name: product.title,
    description: product.description,
    image: product.images,
    sku: product.slug,
    mpn: product.id || product.slug,
    brand: {
      "@type": "Brand",
      name: product.brand || "برند شما",
    },
    offers: {
      "@type": "Offer",
      priceCurrency: "IRR",
      price: formatPriceForSchema(product.price),
      priceValidUntil: new Date(
        new Date().setFullYear(new Date().getFullYear() + 1)
      )
        .toISOString()
        .split("T")[0],
      itemCondition: "https://schema.org/NewCondition",
      availability:
        product.inStock !== false
          ? "https://schema.org/InStock"
          : "https://schema.org/OutOfStock",
      url: `https://allmaxsport.com/products/${product.slug}`,
      seller: {
        "@type": "Organization",
        name: "فروشگاه شما",
      },
      shippingDetails: {
        "@type": "OfferShippingDetails",
        shippingRate: {
          "@type": "MonetaryAmount",
          value: "0",
          currency: "IRR",
        },
        deliveryTime: {
          "@type": "ShippingDeliveryTime",
          businessDays: {
            "@type": "OpeningHoursSpecification",
            dayOfWeek: [
              "https://schema.org/Saturday",
              "https://schema.org/Sunday",
              "https://schema.org/Monday",
              "https://schema.org/Tuesday",
              "https://schema.org/Wednesday",
              "https://schema.org/Thursday",
            ],
          },
          transitTime: {
            "@type": "QuantitativeValue",
            minValue: 3,
            maxValue: 10,
            unitCode: "DAY",
          },
        },
      },
    },
    ...(product.rating && {
      aggregateRating: {
        "@type": "AggregateRating",
        ratingValue: product.rating,
        reviewCount: product.reviewCount || 5,
        bestRating: "5",
        worstRating: "1",
      },
    }),
    ...(product.features &&
      product.features.length > 0 && {
        additionalProperty: product.features.map((feature) => ({
          "@type": "PropertyValue",
          name: "Feature",
          value: feature,
        })),
      }),
  }));

  // Create FAQ schema for the products section
  const faqSchema = {
    "@type": "FAQPage",
    mainEntity: [
      {
        "@type": "Question",
        name: "آیا ارسال محصولات رایگان است؟",
        acceptedAnswer: {
          "@type": "Answer",
          text: "بله، برای سفارش‌های بالای 300 میلیون تومان ارسال رایگان انجام می‌شود.",
        },
      },
      {
        "@type": "Question",
        name: "آیا امکان بازگشت محصول وجود دارد؟",
        acceptedAnswer: {
          "@type": "Answer",
          text: "بله، تا ۷ روز پس از دریافت محصول، امکان بازگشت در صورت عدم رضایت وجود دارد.",
        },
      },
      {
        "@type": "Question",
        name: "مدت زمان ارسال چقدر است؟",
        acceptedAnswer: {
          "@type": "Answer",
          text: "محصولات معمولاً بین 3 تا 10 روز کاری به دست شما می‌رسند.",
        },
      },
    ],
  };

  // Create itemList schema for the carousel
  const itemListSchema = {
    "@type": "ItemList",
    itemListElement: products.map((product, index) => ({
      "@type": "ListItem",
      position: index + 1,
      item: {
        "@id": `https://allmaxsport.com/products/${product.slug}#product`,
      },
    })),
  };

  // Create specialized offer schema for the featured products section
  const specialOfferSchema = {
    "@type": "SpecialAnnouncement",
    name: "پیشنهاد ویژه محصولات",
    text: "تخفیف ویژه محدود برای محصولات منتخب",
    datePosted: new Date().toISOString(),
    expires: new Date(
      new Date().setMonth(new Date().getMonth() + 1)
    ).toISOString(),
    category: "https://schema.org/SaleEvent",
    announcementLocation: {
      "@type": "LocalBusiness",
      name: "فروشگاه شما",
      url: "https://allmaxsport.com",
    },
  };

  // Create organization schema
  const organizationSchema = {
    "@type": "Organization",
    "@id": "https://allmaxsport.com/#organization",
    name: "فروشگاه شما",
    url: "https://allmaxsport.com",
    logo: {
      "@type": "ImageObject",
      url: "https://allmaxsport.com/logo.png",
      width: "180",
      height: "60",
    },
    contactPoint: {
      "@type": "ContactPoint",
      telephone: "+989392566494",
      contactType: "customer service",
      availableLanguage: ["Persian", "English"],
    },
    sameAs: [
      "https://www.instagram.com/allmaxsport/",
      "https://twitter.com/allmaxsport",
      "https://www.facebook.com/allmaxsport",
    ],
  };

  // Create webpage schema with enhanced markup
  const webPageSchema = {
    "@type": "WebPage",
    "@id": "https://allmaxsport.com/featured-products#webpage",
    url: "https://allmaxsport.com/featured-products",
    name: "محصولات ویژه | تخفیف محدود | فروشگاه شما",
    description:
      "جدیدترین محصولات ویژه ما را با تخفیف استثنایی، کیفیت بالا و ارسال رایگان بررسی کنید.",
    isPartOf: {
      "@id": "https://allmaxsport.com/#website",
    },
    primaryImageOfPage: {
      "@type": "ImageObject",
      url:
        products[0]?.images?.[0] ||
        "https://allmaxsport.com/featured-products.jpg",
    },
    speakable: {
      "@type": "SpeakableSpecification",
      cssSelector: ["#products-heading"],
    },
    mainEntity: {
      "@id": `https://allmaxsport.com/featured-products#itemlist`,
    },
  };

  // Create website schema with search functionality
  const websiteSchema = {
    "@type": "WebSite",
    "@id": "https://allmaxsport.com/#website",
    url: "https://allmaxsport.com",
    name: "فروشگاه شما",
    publisher: {
      "@id": "https://allmaxsport.com/#organization",
    },
    potentialAction: {
      "@type": "SearchAction",
      target: {
        "@type": "EntryPoint",
        urlTemplate: "https://allmaxsport.com/search?q={search_term_string}",
      },
      "query-input": "required name=search_term_string",
    },
  };

  return (
    <script
      type="application/ld+json"
      dangerouslySetInnerHTML={{
        __html: JSON.stringify(
          {
            "@context": "https://schema.org",
            "@graph": [
              ...productSchema,
              {
                "@id": `https://allmaxsport.com/featured-products#itemlist`,
                ...itemListSchema,
              },
              faqSchema,
              specialOfferSchema,
              organizationSchema,
              webPageSchema,
              websiteSchema,
            ],
          },
          null,
          0
        ), // Removing indentation to save bytes
      }}
    />
  );
}
