import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { List, ShoppingCart } from "lucide-react";
import { useRef, useState, useEffect } from "react";
import toast from "react-hot-toast";
import Button from "../../Button";
import ProductCard from "./ProductCard";
import StructuredData from "./StructuredData";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

export default function ProductList({ onAddToCart }) {
  const sectionRef = useRef(null);
  const navigationPrevRef = useRef(null);
  const navigationNextRef = useRef(null);
  const [products, setProducts] = useState([]);

  useEffect(() => {
    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/products"
        : "https://allmaxsport.com/api/products/";

    async function fetchProducts() {
      try {
        const response = await fetch(API_URL);
        const data = await response.json();
        setProducts(data);
      } catch (error) {
      }
    }
    fetchProducts();
  }, []);

  function handleAddToCart(id) {
    const shoppingBasket =
      JSON.parse(localStorage.getItem("shoppingBasket")) || [];
    const product = products.find((p) => p.id === id);

    const existingProduct = shoppingBasket.find(
      (item) => item.ProductId === product.id
    );

    if (existingProduct) {
      existingProduct.quantity += 1;
    } else {
      shoppingBasket.push({
        id: shoppingBasket.length + 1,
        ProductId: product.id,
        productName: product.name,
        quantity: 1,
        price: Number(product.price),
        imageUrl: product.images[0],
        description: product.description,
      });
    }

    localStorage.setItem("shoppingBasket", JSON.stringify(shoppingBasket));
    onAddToCart(shoppingBasket.length);
  }

  // Format price function to handle large numbers
  const formatPrice = (price) => {
    // Remove non-digit characters
    const numericPrice = String(price).replace(/[^\d]/g, "");
    // Convert to number
    const priceNum = Number(numericPrice);

    // Format with commas for thousands separators
    return new Intl.NumberFormat("fa-IR").format(priceNum);
  };

  // Process products to format prices
  const processedProducts = products.map((product) => ({
    ...product,
    formattedPrice: formatPrice(product.price),
    formattedOriginalPrice: product.originalPrice
      ? formatPrice(product.originalPrice)
      : null,
  }));

  // Calculate schema.org breadcrumb data
  const breadcrumbData = {
    "@context": "https://schema.org",
    "@type": "BreadcrumbList",
    itemListElement: [
      {
        "@type": "ListItem",
        position: 1,
        name: "صفحه اصلی",
        item: "https://yourdomain.com",
      },
      {
        "@type": "ListItem",
        position: 2,
        name: "محصولات ویژه",
        item: "https://yourdomain.com/featured-products",
      },
    ],
  };

  return (
    <section
      ref={sectionRef}
      className="w-full py-16 px-4 md:px-8 lg:px-12 bg-gradient-to-br from-white via-blue-50 to-white"
      aria-labelledby="products-heading"
    >
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-12">
          <span className="bg-blue-100 text-blue-800 text-xs font-semibold mr-2 px-3 py-1.5 rounded-full uppercase tracking-wider">
            پیشنهاد ویژه
          </span>

          <h2
            id="products-heading"
            className="font-extrabold text-3xl md:text-4xl lg:text-5xl text-gray-900 mt-4 mb-3"
          >
            <span className="relative inline-block">
              محصولات ویژه
              <span className="absolute -bottom-2 left-0 w-full h-2 bg-yellow-300 opacity-60"></span>
            </span>
          </h2>

          <p className="text-center text-gray-600 max-w-2xl mx-auto text-sm md:text-base">
            برترین محصولات با کیفیت بالا و قیمت استثنایی، همراه با ارسال رایگان
          </p>
        </div>

        {/* JSON-LD STRUCTURED DATA FOR SEO */}
        <StructuredData products={products} />

        <div className="relative mt-8 md:mt-12 products-carousel-container">
          {/* Custom navigation buttons outside of Swiper component to avoid duplication */}
          <button
            ref={navigationNextRef}
            className="absolute top-1/2 -translate-y-1/2 -left-4 md:-left-6 z-20 w-12 h-12 flex items-center justify-center bg-white rounded-full shadow-lg hover:bg-gray-100 transition-all duration-200 border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-400"
            aria-label="نمایش محصولات قبلی"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="text-gray-700"
            >
              <path d="m15 18-6-6 6-6" />
            </svg>
          </button>

          <button
            ref={navigationPrevRef}
            className="absolute top-1/2 -translate-y-1/2 -right-4 md:-right-6 z-20 w-12 h-12 flex items-center justify-center bg-white rounded-full shadow-lg hover:bg-gray-100 transition-all duration-200 border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-400"
            aria-label="نمایش محصولات بعدی"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="text-gray-700"
            >
              <path d="m9 18 6-6-6-6" />
            </svg>
          </button>

          <div className="px-4 md:px-8 overflow-hidden">
            <Swiper
              modules={[Navigation, Pagination]}
              navigation={{
                prevEl: navigationPrevRef.current,
                nextEl: navigationNextRef.current,
              }}
              onBeforeInit={(swiper) => {
                // Assign the refs to Swiper's navigation after component mounts
                swiper.params.navigation.prevEl = navigationPrevRef.current;
                swiper.params.navigation.nextEl = navigationNextRef.current;
                swiper.navigation.init();
                swiper.navigation.update();
              }}
              loop
              spaceBetween={16}
              slidesPerView={1.2}
              grabCursor={true}
              speed={700}
              watchSlidesProgress={true}
              autoplay={{
                delay: 100,
                disableOnInteraction: false,
              }}
              breakpoints={{
                320: { slidesPerView: 1.2, spaceBetween: 12 },
                480: { slidesPerView: 1.5, spaceBetween: 16 },
                640: { slidesPerView: 2.2, spaceBetween: 16 },
                768: { slidesPerView: 2.5, spaceBetween: 20 },
                1024: { slidesPerView: 3.2, spaceBetween: 24 },
                1280: { slidesPerView: 4, spaceBetween: 24 },
              }}
              className="products-slider !overflow-visible py-6"
            >
              {processedProducts.map((product, i) => (
                <SwiperSlide className="!h-auto" key={product.name || i}>
                  <ProductCard
                    product={product}
                    index={i}
                    onAddToCart={handleAddToCart}
                  />
                </SwiperSlide>
              ))}
            </Swiper>

            {/* Custom pagination */}
            {/* <div className="swiper-pagination mt-8 !bottom-0"></div> */}
          </div>

          <div className="mt-12 text-center">
            <Link to={"/products"}>
              <Button className=" ">
                <List size={18} className="" />
                <span>نمایش همه محصولات</span>
              </Button>
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
}
