import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";
import { ShoppingCart } from "lucide-react";

const ProductCard = ({ product, index, onAddToCart }) => {
  const [hovered, setHovered] = useState(false);

  const formattedSlug = encodeURIComponent(
    product.name.replace(/\s+/g, "-").toLowerCase()
  );

  // Preload second image
  useEffect(() => {
    if (product.images && product.images.length > 1) {
      const img = new Image();
      img.src = product.images[1];
    }
  }, [product.images]);

  const cardVariants = {
    initial: { opacity: 0, y: 20 },
    animate: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.5, delay: index * 0.08 },
    },
  };

  const hasDiscount = product.formattedOriginalPrice;
  const discountPercentage = hasDiscount
    ? Math.round(((product.price - product.sales_price) / product.price) * 100)
    : 0;

  return (
    <motion.article
      initial="initial"
      animate="animate"
      variants={cardVariants}
      className="group relative bg-white rounded-2xl overflow-hidden shadow-lg hover:shadow-xl transition-all duration-300 border border-gray-100 flex flex-col"
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      itemScope
      itemType="https://schema.org/Product"
    >
      <Link
        to={`/products/${formattedSlug}`}
        aria-label={`مشاهده محصول ${product.name}`}
        className="flex flex-col h-full"
      >
        {/* Badges */}
        <div className="absolute top-3 left-3 z-20 flex flex-col gap-2 rtl">
          {hasDiscount && (
            <div className="bg-red-500 text-white text-xs font-bold px-3 py-1.5 rounded-full shadow-md">
              {discountPercentage}% تخفیف
            </div>
          )}
          {product.isNew && (
            <div className="bg-green-500 text-white text-xs font-bold px-3 py-1.5 rounded-full shadow-md">
              جدید
            </div>
          )}
        </div>

        {/* Images */}
        <div className="relative overflow-hidden bg-gray-50 pt-[75%]">
          <div className="absolute inset-0 w-full h-full">
            {/* Main image */}
            <motion.img
              src={product.images[0]}
              alt={`تصویر اصلی ${product.name}`}
              loading="lazy"
              className="absolute inset-0 w-full h-full object-contain transition-all duration-700 ease-out"
              animate={{ opacity: hovered ? 0 : 1, scale: hovered ? 1.05 : 1 }}
              transition={{ duration: 0.5 }}
            />

            {/* Hover image */}
            {product.images[1] && (
              <motion.img
                src={product.images[1]}
                alt={`تصویر دوم ${product.name}`}
                loading="eager"
                className="absolute inset-0 w-full h-full object-contain transition-all duration-700 ease-out"
                animate={{
                  opacity: hovered ? 1 : 0,
                  scale: hovered ? 1.05 : 1,
                }}
                transition={{ duration: 0.5 }}
              />
            )}
          </div>

          {/* Overlay when hover */}
          <motion.div
            className="absolute inset-0 bg-gradient-to-t from-black/30 via-transparent to-transparent"
            initial={{ opacity: 0 }}
            animate={{ opacity: hovered ? 0.3 : 0 }}
            transition={{ duration: 0.4 }}
          />
        </div>

        {/* Product content */}
        <div className="p-4 flex flex-col flex-grow text-right">
          <div className="flex-grow">
            <h3
              className="text-lg font-bold text-gray-800 group-hover:text-blue-700 transition-colors duration-200 line-clamp-1"
              itemProp="name"
            >
              {product.name}
            </h3>

            <p
              className="text-xs text-gray-600 line-clamp-2 mt-1 mb-3"
              itemProp="description"
            >
              {product.description}
            </p>

            {product.features && product.features.length > 0 && (
              <ul className="hidden md:block text-xs text-gray-500 space-y-1">
                {product.features.slice(0, 2).map((feature, i) => (
                  <li key={i} className="flex items-center gap-1">
                    <span className="inline-block w-1.5 h-1.5 bg-blue-500 rounded-full"></span>
                    {feature}
                  </li>
                ))}
              </ul>
            )}
          </div>

          {/* Price */}
          <div className="mt-4 border-t pt-3 border-gray-100">
            <div className="flex items-center justify-between">
              <span className="bg-blue-100 text-blue-700 text-xs px-2 py-1 rounded">
                {product.category || "محصول"}
              </span>

              <div className="text-left">
                {hasDiscount && (
                  <div className="text-xs text-gray-400 line-through">
                    {Number(product?.sales_price).toLocaleString("fa-IR")} تومان
                  </div>
                )}
                <div className="flex items-center">
                  <span
                    className="text-base font-bold text-gray-900"
                    itemProp="offers"
                    itemScope
                    itemType="https://schema.org/Offer"
                  >
                    <span itemProp="price">
                      {Number(product?.price).toLocaleString("fa-IR")}
                    </span>
                    <meta itemProp="priceCurrency" content="IRR" />
                  </span>
                  <span className="text-xs font-medium text-gray-700 mr-1">
                    تومان
                  </span>
                </div>
              </div>
            </div>
          </div>

          {/* Add to cart button */}
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="mt-4 flex items-center justify-center gap-2 bg-blue-600 hover:bg-blue-700 text-white text-xs font-medium py-2 px-4 rounded-full w-full transition-colors duration-200"
            onClick={(e) => {
              e.preventDefault();
              e.stopPropagation();
              onAddToCart(product.id);
            }}
          >
            <ShoppingCart size={16} />
            افزودن به سبد خرید
          </motion.button>
        </div>
      </Link>
    </motion.article>
  );
};

export default ProductCard;
